@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
  font-style: normal;
  font-weight: 500;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
  font-style: normal;
  font-weight: 600;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0 auto;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f9fbfd;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.5;
  color: #011c40;
  display: flex;
  flex-direction: column;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email], [type=number], [type=password], [type=search], [type=tel], [type=text], [type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.header {
  margin: 0;
  padding-top: 35px;
  padding-left: 103px;
  padding-right: 95px;
  flex-shrink: 0;
  flex-grow: 0;
  position: relative;
}
@media (max-width: 767px) {
  .header {
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 11px;
  }
}
@media (max-width: 1023px) {
  .header {
    padding-left: 45px;
    padding-right: 35px;
    padding-top: 21px;
  }
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
@media (max-width: 767px) {
  .header__wrapper {
    align-items: center;
  }
}

.header__check {
  opacity: 0;
  position: absolute;
}

.header__button {
  display: none;
}
@media (max-width: 767px) {
  .header__button {
    display: flex;
    align-items: center;
    width: 24px;
    height: 24px;
    position: relative;
    cursor: pointer;
    z-index: 1;
    background-color: transparent;
  }
}

.header__button > span,
.header__button > span::before,
.header__button > span::after {
  display: block;
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #ffffff;
  transition-duration: 0.25s;
}

.header__button > span::before {
  content: "";
  top: -8px;
}

.header__button > span::after {
  content: "";
  top: 8px;
}

.header__check:checked ~ .header__button > span {
  transform: rotate(45deg);
}

.header__check:checked ~ .header__button > span::before {
  top: 0;
  transform: rotate(0);
  background-color: #011c40;
}

.header__check:checked ~ .header__button > span::after {
  top: 0;
  transform: rotate(90deg);
  background-color: #011c40;
}

.header__check:checked ~ .navigation {
  display: block;
}

.header__menu-wrap {
  padding-top: 5px;
}

.header__background {
  background-image: url("../img/background.webp");
  background-repeat: no-repeat;
  position: absolute;
  background-size: cover;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  height: 100%;
}
@media (max-width: 767px) {
  .header__background {
    background-image: url("../img/backgroundmobile.webp");
  }
}
@media (max-width: 1023px) {
  .header__background {
    background-image: url("../img/backgroundtablet.webp");
  }
}

@media (max-width: 767px) {
  .navigation {
    display: flex;
  }
}

@media (max-width: 767px) {
  .navigation--header {
    display: none;
    position: absolute;
    background-color: #ffffff;
    top: 0;
    left: 0;
    right: 0;
    transition-duration: 0.25s;
    margin-left: 35px;
    padding-left: 15px;
    padding-right: 30px;
    padding-bottom: 300px;
  }
}

.navigation__list {
  list-style: none;
  display: flex;
  gap: 13px;
  margin: 0;
  padding: 0;
  flex-wrap: wrap;
}
@media (max-width: 1023px) {
  .navigation__list {
    gap: 4px;
  }
}

.navigation__link {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 1;
  color: #ffffff;
  padding: 10px;
}
@media (max-width: 1023px) {
  .navigation__link {
    font-size: 14px;
    line-height: 17px;
    gap: 23px;
  }
}

.navigation__item {
  transition-duration: 0.25s;
}

@media (max-width: 767px) {
  .navigation--header .navigation__list {
    flex-direction: column;
    padding-top: 75px;
    gap: 10px;
  }
}

@media (max-width: 767px) {
  .navigation--header .navigation__link {
    font-size: 18px;
    line-height: 22px;
    color: #011c40;
    padding: 10px;
  }
}

@media (max-width: 767px) {
  .navigation--footer .navigation__list {
    display: flex;
    flex-direction: column;
  }
}

.navigation--footer {
  margin-top: 0;
  margin-bottom: 0;
}
@media (max-width: 767px) {
  .navigation--footer {
    margin-top: 20px;
    margin-bottom: 23px;
  }
}
@media (max-width: 1023px) {
  .navigation--footer {
    margin-top: 2px;
  }
}

@media (max-width: 767px) {
  .navigation--footer .navigation__link {
    padding-left: 0;
  }
}
@media (max-width: 1023px) {
  .navigation--footer .navigation__link {
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
  }
}

.navigation__link:active {
  color: #1e1ed0;
}

.navigation__link:hover {
  color: #f1c232;
}

.advantages {
  background-image: url("../img/background-adventages.webp");
