}

@media (max-width: 1023px) {
  .advantages__item svg {
    width: 22px;
  }
}

@media (max-width: 1023px) {
  .advantages__item--different {
    padding: 25px 17px 20px 20px;
  }
}

@media (max-width: 1023px) {
  .advantages__item--different h3 {
