.catalog {
  @include vp-767 {
    padding-left: 25px;
    padding-right: 25px;
    padding-top: 57px;
    padding-bottom: 65px;
  }

  @include vp-1023 {
    padding-top: 90px;
    padding-bottom: 98px;
    padding-left: 60px;
    padding-right: 60px;
  }

  padding-top: 86px;
  padding-bottom: 120px;
  padding-left: 40px;
  padding-right: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.catalog__title {
  @include vp-767 {
    margin-bottom: 30px;
    font-size: 24px;
  }

  @include vp-1023 {
    font-size: 26px;
  }

  margin: 0;
  font-weight: 600;
  font-size: 28px;
  line-height: 1.5;
  margin-bottom: 48px;
}

.catalog__list {
  @include vp-767 {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 29px;
  }

  @include vp-1023 {
    grid-template-columns: repeat(2, 1fr);
    width: 100%;
  }

  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
}
